<template>
  <div class="layout">
    <top></top>
    <div class="flex">
      <div class="layout-menu-left">
        <div class="layout-logo-left">
          <div class="app_name">
            <div style="margin-left: -0.4rem;flex: 0 0 0.05rem;">
              <img :src='pulishState["pic"]'>
            </div>
            <div style="margin-left: 0.2rem;margin-bottom: 0.2rem;">
              <span v-text='pulishState["name"]' style="display: inline-block;margin-bottom: 0.05rem;font-size: 0.15rem;"></span><br />
              <!--0授权 3发布 -->
              <span class="send" v-if="publish===0">未发布</span>
              <span class="send" v-if="publish===1">已绑定</span>
              <span class="send" v-if="publish===2">代码已上传</span>
              <span class="send" v-if="publish===3">审核中</span>
              <span class="send" v-if="publish===4">已上线</span>
            </div>
          </div>
          <div>
            <img src="../assets/boundary.png" />
          </div>
        </div>
        <ul class="layout-aside">
          <li @click="info(1)" @mouseover="enter(1)" @mouseout="out(1)">
            <div class="right">
              <div class="iconLab">
                <i class="iconfont icon-fangjianhao i"></i>
              </div>
              <span>房间</span>
            </div>
            <div v-if="item[1].show">
              <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <li @click="info(2)" @mouseover="enter(2)" @mouseout="out(2)">
            <div class="right">
              <div class="iconLab">
                <i class="iconfont icon-pinpai"></i>
              </div>
              <span>品牌信息</span>
            </div>
            <div v-if="item[2].show">
               <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <li @click="info(3)" @mouseover="enter(3)" @mouseout="out(3)">
            <div class="right">
              <div class="iconLab">
                <i class="iconfont icon-weixinzhifu"></i>
              </div>
              <span>微信支付</span>
            </div>
            <div v-if="item[3].show">
               <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <li @click="info(4)" @mouseover="enter(4)" @mouseout="out(4)">
            <div class="right">
              <div class="iconLab">
                <i class="iconfont icon-icon i"></i>
              </div>
              <span>风格</span>
            </div>
            <div v-if="item[4].show">
               <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <transition name="slideDown">
            <div v-show="expertShow">
              <li @click="info(5)" @mouseover="enter(5)" @mouseout="out(5)">
                <div class="right">
                  <div class="iconLab">
                    <i class="iconfont icon-jiage i"></i>
                  </div>
                  <span>价格调整</span>
                </div>
                <div v-if="item[5].show">
                    <i class="iconfont icon-jiantou i"></i>
                </div>
              </li>
              <li @click="info(6)" @mouseover="enter(6)" @mouseout="out(6)">
                <div class="right">
                  <div class="iconLab">
                    <i class="iconfont icon-tongzhi" style="font-size: 0.18rem;"></i>
                  </div>
                  <span>通知提醒</span>
                </div>
                <div v-if="item[6].show">
                   <i class="iconfont icon-jiantou i"></i>
                </div>
              </li>
              <li @click="info(7)" @mouseover="enter(7)" @mouseout="out(7)">
                <div class="right">
                  <div class="iconLab">
                    <i class="iconfont icon-fenlei i"></i>
                  </div>
                  <span>多门店管理</span>
                </div>
                <div v-if="item[7].show">
                   <i class="iconfont icon-jiantou i"></i>
                </div>
              </li>
              <li @click="info(8)" @mouseover="enter(8)" @mouseout="out(8)">
                <div class="right">
                  <div class="iconLab">
                    <i class="iconfont icon-yonghuguanli i"></i>
                  </div>
                  <span>用户管理</span>
                </div>
                <div v-if="item[8].show">
                   <i class="iconfont icon-jiantou i"></i>
                </div>
              </li>
              <li @click="info(9)" @mouseover="enter(9)" @mouseout="out(9)">
                <div class="right">
                  <div class="iconLab">
                    <i class="iconfont icon-dingdan-copy i"></i>
                  </div>
                  <span>订单管理</span>
                </div>
                <div v-if="item[9].show">
                   <i class="iconfont icon-jiantou i"></i>
                </div>
              </li>
            </div>
          </transition>
          <li @click.prevent="chan" @mouseover="enter(10)" @mouseout="out(10)">
            <div class="order_conceal right">
              <div class="iconLab">
                <Icon :type="!expertShow?'arrow-down-b':'arrow-up-b'"></Icon>
              </div>
              <span>{{showExpert}}</span>
            </div>
          </li>
        </ul>
        <div class="btn">
          <a href="javascript:void(0)" @click="submitApp" v-if="publish===0">
            <span>授权小程序</span>
          </a>
          <a href="JavaScript:void(0)" style="cursor: not-allowed" v-else-if="publish===3">
            <span>审核中...</span>
          </a>
          <a href="JavaScript:void(0)" @click="submitApp" v-else-if="publish===1">
            <span>提交代码包</span>
          </a>
          <a href="JavaScript:void(0)" @click="submitApp" v-else>
            <span>立即发布</span>
          </a>
          <a href="JavaScript:void(0)" @click="closeEditor" class="list">
            <span>退出编辑器</span>
          </a>
          <!--<footer>
            <a href="JavaScript:void(0)" @click="closeEditor">
              <Icon type="android-exit" size="16" style="margin-right: 0.02rem;"></Icon>
              <span>退出编辑器</span>
            </a>
            <a href="JavaScript:void(0)">
              <Icon type="document-text" size="16" style="margin-right: 0.02rem;"></Icon>
              <span>帮助文档</span>
            </a>
          </footer>-->
        </div>
      </div>
      <div class="content">
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </div>
      <div class="photo-box" v-if="pShow">
        <home></home>
      </div>
    </div>
    <Modal v-model="audit">
      <p slot="header" style="text-align:left">
        <span>小程序</span>
      </p>
      <div style="text-align:center;" class="program_content">
        <Icon type="android-time" size="50" color="#15bbfc"></Icon>
        <h2>已成功提交审核</h2>
        <p style="color:#90969e;font-size: 0.14rem;padding: 0.05rem">审核大约需要1-2天,结果将通过微信告知。</p>
        <p style="color:#90969e;font-size: 0.14rem">若审核通过,约2小时后将发布生效</p>
        <a href="javascript:void(0)" @click="audit=false"> 好的 </a>
      </div>
      <div slot="footer">
        <p style="display: flex;justify-content: center;align-items: center;padding: 0.1rem 0">
        </p>
      </div>
    </Modal>
    <Modal v-model="submit">
      <p slot="header" style="text-align:left">
        <span>小程序</span>
      </p>
      <div style="text-align:center;" class="program_content">
        <img :src="programs[0].src" alt="" style="width: 50px;max-height: 100%">
        <h2>{{programs[0].name}}</h2>
        <p style="color:#666666;font-size: 0.13rem">{{programs[0].title}}</p>
        <a href="javascript:void(0)" v-if="!msgBtn" @click="register">注册小程序账号</a>
        <a href="javascript:void(0)" @click="go" style="background-color:darkgrey;" v-if="!msgBtn">已有账号,前往小程序授权</a>
        <a href="javascript:void(0)" @click="success" v-if="msgBtn">成功授权</a>
        <a href="javascript:void(0)" @click="go" style="background-color: orangered;" v-if="msgBtn">授权失败,重新授权</a>
      </div>
      <div slot="footer">
        <p style="display: flex;justify-content: center;align-items: center;padding: 0.1rem 0">
          <Icon type="information-circled" size="18"></Icon> <span style="margin-left: 0.1rem;font-size: 0.13rem;color: #666666">提醒：请授权微信小程序账号，非公众号。</span>
        </p>
      </div>
    </Modal>
  </div>
</template>
<script>
  import info from './subscribe/serveInfo/serveInfo.vue'
  import home from './phone/hotel.vue'
  import top from './totalHead.vue'
  export default {
    name: "hotel",
    data() {
      return {
        theme: "light",
        msgBtn: false,
        programs: [{
            name: "微信小程序授权",
            title: '首次发布将需要进行小程序授权，请使用小程序管理员账号进行操作。',
            src: require('../assets/icon-wechat.svg')
          },
          {
            name: "微信小程序未进行微信认证",
            title: '未完成微信认证的小程序账号将无法使用微信支付功能',
            src: require('../assets/icon-wechat-verification.svg'),
          }
        ],
        txMsg: true,
        submit: false,
        //      审核
        audit: false,
        appid: '',
        modal: false,
        expertShow: false, // 显示高级选项
        showExpert: "显示高级选项",
        //      选中的
        num: 1,
        item: [{
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        },{
          show:false
        }],
        list() {
          const lists = document.getElementsByClassName('layout-aside')[0].getElementsByTagName('li');
          return lists
        },
        //状态
      }
    },
    computed: {
      publish() {
        return this.$store.getters.getPublish;
      },
      pShow() {
        return this.$store.getters.getStatus;
      },
      pulishState() {
        if(this.getSession["state"] === undefined || this.getSession["state"] !== 3) {
          return this.getSession = this.$store.getters.getBaseInfo;
        } else {
          if(this.$store.getters.getBaseInfo["pic"] === undefined) {
            return this.getSession
          } else {
            return this.getSession = this.$store.getters.getBaseInfo;
          }
        }
      },
    },
    methods: {
      submitApp() {
        this.post('custom/Own/getAppInfo', {
          author:1
        }).then(res => {
          if(res.data.code === 10000) {
            if(res.data.data.is_publish === 0) {
              return this.submit = true;
            } else if(res.data.data.is_publish === 2) {
              return this.post('/custom/weapp/submit_audit', {}).then(res => {
                if(res.data.code === 10000) {
                  this.audit = true;
                  this.$store.dispatch('publish', res.data.data.is_publish);
                } else {
                  const content = res.data.msg + "请重新发布";
                  this.$Modal.error({
                    title: "发布小程序",
                    content: content
                  });
                }
              })
            } else if(res.data.data.is_publish === 1) {
              return this.post('/custom/weapp/commit', {}).then(res => {
                if(res.data.code === 10000) {
                  this.$store.dispatch('publish', res.data.data.is_publish);
                } else {
                  const content = " 上传代码包失败，请重新上传";
                  this.$Modal.error({
                    title: "小程序",
                    content: content
                  });
                }
              })
            }
          } else {
            this.$Modal.error({
              title: "小程序授权失败",
              content: res.data.msg
            });
          }
        });
      },
      //    箭头显示
      imgShow(val) {
        this.item.map(list => {
          return list.show = false
        });
        this.item[val].show = true;
      },
      chan() {
        this.expertShow = !this.expertShow;
        this.expertShow ? this.showExpert = "隐藏高级选项" : "显示高级选项"
      },
//    orderList() {
//      this.$router.push(`/${this.appid}/hotel/hotelList`);
//    },
      //      close展开项
      info(val) {
        this.num = val;
        this.item.map(list => {
          return list.show = false
        });
        this.item[val].show = true;
        let list = this.list();
        for(let item of list) {
          item.className = '';
        }
        this.$store.commit('setStatus', true);
        switch(val) {
          case 1:
            list[val - 1].className = 'active';
            this.$router.push(`/${this.appid}/hotel/hotelRoom`);
            this.$store.commit('setStatus', true);
            break;
          case 2:
            list[val - 1].className = 'active';
            this.$router.push(`/${this.appid}/hotel/hotelSign`);
            this.$store.commit('setStatus', true);
            break;
          case 3:
            list[val - 1].className = 'active';
            this.$router.push(`/${this.appid}/hotel/pay`);
            this.$store.commit('setStatus', true);
            break;
          case 4:
            list[val - 1].className = 'active';
            this.$router.push(`/${this.appid}/hotel/hotelStyle`);
            this.$store.commit('setStatus', true);
            break;
          case 5:
            list[val - 1].className = 'active';
            this.$router.push(`/${this.appid}/hotel/hotelPrice`);
            this.$store.commit('setStatus', true);
            break;
          case 6:
            list[val - 1].className = 'active';
            this.$router.push(`/${this.appid}/hotel/inform`);
            this.$store.commit('setStatus', true);
            break;
          case 7:
            list[val - 1].className = 'active';
            this.$router.push(`/${this.appid}/hotel/store`);
            this.$store.commit('setStatus', true);
            break;
          case 8:
            list[val - 1].className = 'active';
            this.$router.push(`/${this.appid}/hotel/hotelUser`);
            this.$store.commit('setStatus', false);
            break;
          case 9:
            list[9].className = 'active';
            this.$router.push(`/${this.appid}/hotel/hotelList`);
            this.$store.commit('setStatus', false);
            break;
        }
      },
      enter(val) {
        this.item[val].show = true;
      },
      out(val) {
        this.item[val].show = false;
        this.item[this.num].show = true;
      },
      closeEditor() {
        this.$router.push('/establish');
        sessionStorage.removeItem('appid');
      },
      go() {
        this.msgBtn = true;
        let url = `http://weapp.xiguawenhua.com/weixin/component/auth_jump?apps=${this.appid}`;
        window.open(url, self);
      },
      register(){
        let url = 'https://mp.weixin.qq.com/wxopen/waregister?action=step1';
        window.open(url, self);
      },
      success() {
        location.reload();
      }
    },
    components: {
      home,
      info,
      top
    },

    beforeRouteEnter(to, from, next) {
      next(vm => {
        if(to.path.indexOf('inform') !== -1 || to.path.indexOf('hotelPrice') !== -1 || to.path.indexOf('store') !== -1) {
          vm.expertShow = true
          vm.expertShow = '隐藏高级选项'
        }
      })
    },
    mounted() {
      this.$store.dispatch("fetch_load", false);
      this.$store.dispatch("localBaseInfo");
      this.appid = this.$route.params.appid;
      let name = this.$route.name;
      let list = this.list();
      switch(name) {
        case 'hotelRoom':
          list[0].className = 'active';
          this.imgShow(1);
          this.num = 1;
          break;
        case 'hotelInfo':
          list[1].className = 'active';
          this.imgShow(2);
          this.num = 2;
          break;
        case 'hotelPay':
          list[2].className = 'active';
          this.imgShow(3);
          this.num = 3;
          break;
        case 'hotelStyle':
          list[3].className = 'active';
          this.imgShow(4);
          this.num = 4;
          break;
        case 'hotelPrice':
          list[4].className = 'active';
          this.imgShow(5);
          this.num = 5;

          break;
        case 'hotelInform':
          list[5].className = 'active';
          this.imgShow(6);
          this.num = 6;
          break;
        case 'store':
          list[6].className = 'active';
          this.imgShow(7);
          this.num = 6;
          break;
        case 'hotelUser':
          list[7].className = 'active';
          this.imgShow(8);
          this.num = 7;
          break;
        case 'hotelList':
          list[8].className = 'active';
          this.imgShow(9);
          this.num = 9;
          break;
      }
    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="scoped">
  @import url("../../static/common.css");
  .active {
    color: #FFFFFF !important;
  }
  .i{
    font-size: 0.15rem;
  }
</style>



