<template>
  <div class="info">
    <!--<topServer v-if="$route.name==='serveInfo'"></topServer>
    <div class="top" v-if="$route.name!=='serveInfo'">
      <header>
        <i class="iconfont icon-info" style="font-size: 0.2rem;margin-right: 0.1rem"></i>
        <span>基本信息</span>
      </header>
    </div>-->
    <ul class="form_serve">
      <li style="margin-top: 0.26rem;">
        <h2 class="h2_serve">店名 <span style="color:#e64751; font-size: 0.12rem;margin-top: 0.1rem;display: inline-block;margin-left: 1.4rem;font-weight: 500;">{{msg}}</span></h2>

        <Tooltip placement="right" content="稍后再'微信小程序'确定名称与图标">
          <input type="text" class="input_serve" v-model.trim="name" @focus="textF" @blur="change">
        </Tooltip>
        <br>

      </li>
      <li>
        <h2 class="h2_serve">图标</h2>
        <div style="display: flex">
          <div @click="upload">
            <img :src="photoed" alt="" style="width: 50px;height: 50px;border-radius: 50%">
          </div>
          <div>
            <a href="javascript:void(0)" style="font-size: 0.15rem;margin-left: 0.1rem;" @click="upload">上传图片</a>
            <div style="font-size: 0.13rem;margin-top: 0.05rem;margin-left: 0.1rem;">(推荐尺寸144*144，大小2M内)</div>
          </div>
        </div>
      </li>

      <li style="display: flex">
        <div style="margin-right: 0.2rem">
          <h2 class="h2_serve">地址</h2>
          <input type="text" class="input_serve" v-model.trim="place" placeholder="请输入省市区详细地址" @focus="textF" @blur="input">
        </div>
        <div>
          <h2 class="h2_serve">客服热线 <span style="color: #e64751;float: right;font-size: 0.12rem;font-weight: 500;">{{currentTel}}</span></h2>
          <input type="number" class="input_serve" v-model.trim="tel" placeholder="输入电话号码" style="width: 2.5rem" @focus="textF" @blur="input('tel')">
        </div>
      </li>
      <li style="display: flex;">
        <div style="margin-right: 0.2rem;">
          <div>
            <h2 class="h2_serve">营业时间</h2>
            <select name="9.00" class="select_time" v-model="start_time">
              <option v-for="time in times" :label="time.value" :value="time.value"></option>
            </select>&nbsp;&nbsp;至&nbsp;&nbsp;
            <select name="18.00" class="select_time" v-model="over_time">
              <option v-for="time in times" :label="time.value" :value="time.value"></option>
            </select>
          </div>
          <div class="reserve_time">
            <span>预定时间仅在营业时间内选择</span>
            <span><input type="checkbox" class="input_serve" v-model="business" />
                                周六、周日不营业
                <Tooltip placement="right" content="勾选后，顾客将不可预订周六、周日" class="Tooltip">
                    <Icon type="help-circled" size="18"></Icon>
                 </Tooltip>
              </span>

          </div>
        </div>
        <div>
          <h2 class="h2_serve">官方网站 <span style="color: #e64751;float: right;font-size: 0.12rem; font-weight: 500;">{{currentUrl}}</span></h2>
          <input type="text" class="input_serve" v-model.trim="webName" placeholder="输入网址(选填)" style="width: 2.5rem" @focus="textF" @blur="input('email')">
        </div>
      </li>
      <li>
        <h2 class="h2_serve">关于我们</h2>
        <textarea cols="30" rows="10" placeholder="简单描述下你的品牌" @focus="textF" @blur="input" v-model.trim="desc"></textarea>
      </li>
      <li>
        <a href="javascript:void(0)" class="a" @click="save" v-if="!load">保存</a>
        <a href="javascript:void(0)" class="a"  v-if="load">保存 <img src="../../../assets/bai.gif" alt="" style="width: 20px;max-height: 100%"></a>
      </li>
    </ul>
    <modal ref="upload"></modal>
  </div>
</template>
<script>
  import topServer from '../topServer.vue'
  import modal from '../../upload/modal.vue'
  export default {
    data() {
      return {
        load:false,
        name: "",
        desc: "",
        tel: '',
        place: '',
        webName: '',
        start_time:'9:00',
        over_time:'18:00',
        msg: '',
        src: '',
        business: '',
        times: [{
            value: '0:00'
          },
          {
            value: '1:00'
          },
          {
            value: '2:00'
          },
          {
            value: '3:00'
          },
          {
            value: '4:00'
          },
          {
            value: '5:00'
          },
          {
            value: '6:00'
          },
          {
            value: '7:00'
          },
          {
            value: '8:00'
          },
          {
            value: '9:00'
          },
          {
            value: '10:00'
          },
          {
            value: '11:00'
          },
          {
            value: '12:00'
          },
          {
            value: '13:00'
          },
          {
            value: '14:00'
          },
          {
            value: '15:00'
          },
          {
            value: '16:00'
          },
          {
            value: '17:00'
          },
          {
            value: '18:00'
          },
          {
            value: '19:00'
          },
          {
            value: '20.00'
          },
          {
            value: '21:00'
          },
          {
            value: '22:00'
          },
          {
            value: '23:00'
          },
          {
            value: '24:00'
          },
        ],
        currentTel: '',
        currentUrl: ''
      }
    },
    computed: {
      photo() {
        return this.$store.getters.getPhoto;
      },
      photoed() {
        if(this.$store.getters.getPhotoed) {
          return this.$store.getters.getPhotoed.pic;
        } else {
          return this.src
        }
      }
    },
    methods: {
//    保存信息
      save() {
        this.load = true;
        let business = this.business ? 1 : 2;
        let obj = {};
        obj["pic"] = this.photo || this.photoed.slice(29);
        obj["pic"] = "http://weapp.xiguawenhua.com/" + obj["pic"];
        obj["name"] = this.name;
        obj["state"] = 2;
        this.$store.dispatch("baseInfo", obj);
        sessionStorage.setItem("baseInfo", JSON.stringify(obj));
        this.post('custom/Own/setAppInfo', {
          name: this.name,
          pic: `${this.photo}` || `${this.photoed.slice(29)}`,
          desc: this.desc,
          tel: this.tel,
          site_url: this.webName,
          address: this.place,
          start_time:this.start_time,
          over_time:this.over_time,
          business:business
        }).then(res => {
          this.load = false;
          if(res.data.code === 10000) {
            const content = "保存成功";
            this.$Modal.success({
              title: "保存成功",
              content: content
            });
          } else {
            const content = "保存失败，请重新提交";
            this.$Modal.error({
              title: "保存失败",
              content: content
            });
          }
        })
      },
      upload() {
        this.$refs.upload.modal = true;
        this.$store.dispatch('goodsPhoto', 0);
      },
//    输入框不能为空
      textF() {
        event.target.style.borderColor = 'skyblue';
      },
      change() {
        if(this.name) {
          window.frames[0].postMessage({
            window: {
              navigationBarTitleText: this.name
            }
          }, "*");
          this.msg = '';
          event.target.style.borderColor = 'gainsboro';
        } else {
          this.msg = '名称不能为空';
          event.target.style.borderColor = 'red';
        }
      },
//    正则验证手机号格式是否正确
      input(val) {
        if(val === "tel") {
          let myreg = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
          !myreg.test(this.tel) ? this.currentTel = '电话号码格式不对' : this.currentTel = ''
        } else if(val === 'email') {
          let url = /[a-zA-Z0-9][-a-zA-Z0-9]{0,62}(\.[a-zA-Z0-9][-a-zA-Z0-9]{0,62})+\.?/;
          !url.test(this.webName) ? this.currentUrl = '网站格式不对' : this.currentUrl = ""
        }
        event.target.style.borderColor = 'gainsboro';
        window.frames[0].postMessage({
          attr: {
            description: this.desc,
            location: this.place,
            phone: this.tel,
            siteUrl: this.webName
          }
        }, "*");
      }
    },
    created() {
      let appid = this.$route.params.appid;
      sessionStorage.setItem('appid', appid);
      this.post('custom/Own/getAppInfo', {}).then(res => {
        if(res.data.code === 10000) {
          this.name = res.data.data.name;
          this.desc = res.data.data.desc;
          this.tel = res.data.data.tel;
          this.webName = res.data.data.site_url;
          this.place = res.data.data.address;
          this.business = res.data.data.business === 1;
          this.start_time = res.data.data.start_time;
          this.over_time = res.data.data.over_time;
          if(res.data.data.pic.indexOf('http') === -1) {
            this.src = "http://weapp.xiguawenhua.com/" + res.data.data.pic;
          } else {
            this.src = res.data.data.pic;
          }
          let obj = {};
          obj["pic"] = "http://weapp.xiguawenhua.com/" + res.data.data.pic;
          obj["name"] = res.data.data.name;
          obj["publish"] = res.data.data.is_publish;
          obj["state"] = 2;
          this.$store.dispatch("baseInfo", obj);
          sessionStorage.setItem("baseInfo", JSON.stringify(obj));
          this.$store.dispatch('publish', res.data.data.is_publish);
//          if(res.data.data.is_publish === 1) {
//            this.post('/custom/weapp/commit', {}).then(res => {
//              if(res.data.code===10000){
//                this.$store.dispatch('publish', res.data.data.is_publish);
//              }else{
//                this.$Modal.error({
//                  title: "失败",
//                  content: res.data
//                });
//              }
//            })
//          }
        }
      })
    },
    components: {
      topServer,
      modal
    }
  }
</script>
<style scoped>
  .form_serve li {
    /*padding: 0.18rem 0;*/
   margin-bottom: 0.26rem;
  }

  .h2_serve {
    font-size: 0.15rem;
    font-weight: 600;
    margin-bottom: 0.05rem;
  }

  .input_serve {
    display: inline-block;
    width: 2.5rem;
    font-size: 0.15rem;
    border-radius: 0.05rem;
    border: 1px solid gainsboro;
    padding: 0.05rem 0.1rem;
  }

  input::-webkit-input-placeholder,textarea::-webkit-input-placeholder{
    /* WebKit browsers*/
    color: #9f9f9f;
    font-size: 0.12rem;
  }

  input:-moz-placeholder,textarea:-moz-placeholder {
    /* Mozilla Firefox 4 to 18*/
    color: #9f9f9f;
    font-size: 0.12rem;
  }

  input::-moz-placeholder,textarea:-moz-placeholder {
    /* Mozilla Firefox 19+*/
    color: #9f9f9f;
    font-size: 0.12rem;
  }

  input:-ms-input-placeholder,textarea:-ms-input-placeholder {
    /* Internet Explorer 10+*/
    color: #9f9f9f;
    font-size: 0.12rem;
  }
  textarea {
    display: inline-block;
    width: 4rem;
    height: 1.5rem;
    border: 1px solid gainsboro;
    border-radius: 0.05rem;
    padding: 0.1rem;
  }

  .a {
    color: white;
    display: inline-block;
    padding: 0.1rem 0.3rem;
    border-radius: 0.05rem;
    background-color: #59A2ED 100%;
  }

  .top {
    padding: 0.1rem 0;
    border-bottom: 1px solid #ebecee
  }

  header {
    display: flex;
    align-items: center
  }

  header>span {
    font-size: 0.19rem
  }

  p {
    padding: 0.1rem 0;
    font-size: 0.14rem;
    letter-spacing: 0.01rem
  }

  .select_time {
    width: 1.1rem;
    height: 0.34rem;
    border-radius: 0.04rem;
    font-size: 0.15rem;
    border: 1px solid gainsboro;
  }

  .reserve_time>span:nth-child(1) {
    margin-top: 0.1rem;
    display: block;
    font-size: 0.12rem;
    color: #747981;
  }

  .reserve_time>span:nth-child(2) {
    margin-top: 0.1rem;
    display: block;
    font-size: 0.15rem;
    color: #333333;
  }

  .reserve_time>span:nth-child(2)>input {
    width: 0.2rem;
  }
  .info{
  background: white;
  padding-top: 0.1rem;
  padding-left: 0.2rem;
  background: white;
  padding-bottom: 0.2rem;
  box-shadow: 5px 5px 10px #E7E7E7;
  }
</style>



